import {AfterViewInit, Component, OnInit} from '@angular/core';
import {Course} from '../entity';
import {CourseService} from '../course.service';
import {ActivatedRoute} from '@angular/router';
import {catchError} from 'rxjs/operators';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.css']
})
export class ChartsComponent implements OnInit, AfterViewInit {
  private courses: Course[];
  private newCourses: string[];
  private countNum: number[];

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService
  ) {
  }

  ngOnInit() {
    this.getCourses();
  }

  ngAfterViewInit(): void {
    const Highcharts = require('highcharts');
    require('highcharts/modules/exporting')(Highcharts);
    // 
    Highcharts.chart('container', {
      chart: {
        type: 'line'
      },
      title: {
        text: ''
      },
      subtitle: {
        text: ' '
      },
      xAxis: {
        categories: ['3', '3', 'JavaWeb', '', '', ''],
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
          text: ''
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f} </b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0
        }
      },
      credits: {
        enabled: false
      },
      series: [{
        name: '',
        data: [64, 58, 61, 49]
      }]
    });

    const Highcharts2 = require('highcharts');
    require('highcharts/modules/exporting')(Highcharts2);
    // 
    Highcharts2.chart('container2', {
      chart: {
        type: 'pie'
      },
      title: {
        text: ''
      },
      subtitle: {
        text: ' '
      },
      xAxis: {
        categories: [],
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
          text: ''
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f} </b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0
        }
      },
      credits: {
        enabled: false
      },
      series: [{
        name: '',
        data: [['', 59], ['', 34], ['', 52], ['', 64]]
      }]
    });
  }

  // Course
  getCourses(): void {
    this.courseService.getCourses()
      .subscribe(res => {
        this.courses = res;
        // tslint:disable-next-line:prefer-for-of
        for (let i = 0; i < this.courses.length; i++) {
        this.newCourses.push(this.courses[i].description);
        }
      });
  }

}
